import React from "react";
import "./Footer.css";
import { Link } from "react-router-dom";

const Footer = () => {
  return (
    <div className="footer">
      <div className="footer-wrapper">
        <div className="footer-top">
          <div className="footer-top-left">
            <div className="footer-top-left-top">
              <h2>Sweet Bunny Cafe</h2>
              <p style={{ marginBottom: "20px" }}>
                Everything's will be better with a coffee and desserts
              </p>
            </div>
            <div className="footer-top-left-bottom">
              <i className="fab fa-facebook-square fb"></i>
              <i className="fab fa-instagram-square ig"></i>
              <i className="fab fa-twitter-square tw"></i>
              <i className="fab fa-linkedin in"></i>
            </div>
          </div>
          <div className="footer-top-right">
            <div className="footer-links">
              <p className="header-links-p">Links</p>
              <ul>
                <Link to="/sweetbunny" className="footerLinks">
                  Home
                </Link>

                <Link to="/sweetbunny/menu" className="footerLinks">
                  Menu
                </Link>
                <Link to="/sweetbunny/about" className="footerLinks">
                  About
                </Link>
              </ul>
            </div>
            <div className="footer-links">
              <p className="header-links-p">Categories</p>
              <ul>
                <Link className="footerLinks">Products</Link>
                <Link to="/sweetbunny/login" className="footerLinks">
                  Login
                </Link>
                <Link to="/sweetbunny/register" className="footerLinks">
                  Register
                </Link>
              </ul>
            </div>
            <div className="footer-links">
              <p className="header-links-p">Contact</p>
              <p className="contact-p">Normal Road Baliwasan Street,</p>
              <p style={{ marginBottom: "10px;" }} className="contact-p">
                Zamboanga City
              </p>
              <p className="contact-p">
                <i>sweetbunnycafe@gmail.com</i>
              </p>
            </div>
          </div>
        </div>
        <div className="footer-btm">
          <div className="footer-btm-left">
            <p> 2021 Sweet Bunny Cafe | All rights reserved</p>
          </div>
          <div className="footer-btm-right">
            <p>Terms of Use</p>
            <p>Privacy Policy</p>
            <p>Cookie Policy</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Footer;
