import React, { useState, useContext, useEffect } from "react";
import "./Navbar.css";
import { Link } from "react-router-dom";
import { ContextFile } from "../../Context/ContextFile";
import Axios from "axios";

const Navbar = (props) => {
  const [scrollPosition, setScrollPosition] = useState(0);
  const handleScroll = () => {
    const position = window.pageYOffset;
    setScrollPosition(position);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll, { passive: true });

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const [badge, setBadge] = useState(0);
  const {
    firstnameState,
    roleState,
    stateState,
    userState,
    cartState,
    numberState,
  } = useContext(ContextFile);

  const [firstname, setFirstname] = firstnameState;
  const [role, setRole] = roleState;
  const [state, setState] = stateState;
  const [userId, setUserId] = userState;
  const [cart, setCart] = cartState;
  const [number, setNumber] = numberState;

  const [dropdown, setDropdown] = useState(false);

  Axios.defaults.withCredentials = true;

  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/login").then((response) => {
      if (response.data.loggedIn === false) {
        setFirstname("");
      } else {
        setFirstname(response.data.firstname);
        setRole(response.data.user);
        setNumber(response.data.contact);
      }
    });
  }, [props.state]);

  const submitLogout = () => {
    Axios.get("https://sweetbunny.herokuapp.com/logout")
      .then((response) => {
        setState("Logout");
        window.location.reload();
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  };

  useEffect(() => {
    Axios.get(`https://sweetbunny.herokuapp.com/add-cart/${userId}`)
      .then((response) => {
        if (response.data.cart) {
          setCart(response.data.cart);
          console.log(response.data);
        }
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  }, [props.state]);

  return (
    <header className={scrollPosition > 300 ? "header-active" : "header"}>
      <nav className="navbar">
        <Link className="brand-name" to="/sweetbunny">
          <h2>Sweet Bunny Cafe</h2>
        </Link>
        <ul className="nav-links">
          <Link
            to="/sweetbunny"
            className={
              window.location.pathname === "/sweetbunny" ? "active-link" : "link"
            }
          >
            Home
          </Link>

          <Link
            to="/sweetbunny/menu"
            className={
              window.location.pathname === "/sweetbunny/menu" ? "active-link" : "link"
            }
          >
            Menu
          </Link>

          <Link
            to="/sweetbunny/cart"
            className={
              window.location.pathname === "/sweetbunny/cart"
                ? "active-link"
                : "link special-link"
            }
          >
            Cart
            <i className="fas fa-shopping-cart">
              <div className="shopping-cart-badge">{cart.length}</div>
            </i>
          </Link>
        </ul>

        {firstname === "" ? (
          <>
            <Link to="/sweetbunny/login" className="link login-link">
              <i style={{ marginRight: "5px" }} className="fas fa-user"></i>{" "}
              Login
            </Link>
          </>
        ) : (
          <>
            <div className="profile-nav">
              <div className="nav-username">{firstname}</div>
              <div
                onClick={() => setDropdown(!dropdown)}
                className={
                  dropdown ? "profile-nav-drop-active" : "profile-nav-drop"
                }
              >
                <i className="fas fa-caret-down"></i>
                {dropdown ? (
                  <div className="profile-nav-drop-after">
                    <div className="profile-nav-drop-after-top">
                      <div className="profile-nav-drop-after-top-wrapper">
                        <div className="left">
                          <i className="fas fa-user"></i>
                        </div>
                        <div className="right">
                          <h5>{firstname}</h5>
                          <p>{role}</p>
                        </div>
                      </div>
                    </div>
                    <div className="profile-nav-drop-after-btm">
                      <Link
                        to="/sweetbunny/order"
                        style={{ textDecoration: "none", color: "#272727" }}
                        className="profile-nav-drop-after-btm1"
                      >
                        <p>Your Orders</p>
                      </Link>
                      <div
                        onClick={submitLogout}
                        className="profile-nav-drop-after-btm2"
                      >
                        <p>Logout</p>
                      </div>
                    </div>
                  </div>
                ) : null}
              </div>
            </div>
          </>
        )}
      </nav>
    </header>
  );
};

export default Navbar;
