import React, { useState, useContext } from "react";
import "./Form.css";
import Axios from "axios";
import { ContextFile } from "../../Context/ContextFile";
import { PropagateLoader } from "react-spinners";

const Form = (props) => {
  const [name, setName] = useState("");
  const [type, setType] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [quantity, setQuantity] = useState("");

  const { messageState, stateState } = useContext(ContextFile);

  const [state, setState] = stateState;

  const [picture, setPicture] = useState("");

  const [after, setAfter] = useState(false);

  const upload = async () => {
    setAfter(true);
    const formData = new FormData();
    formData.append("file", picture);
    formData.append("upload_preset", "ayivjexe");
    formData.append("cloud_name", "defutech-inc");

    fetch("https://api.cloudinary.com/v1_1/defutech-inc/image/upload", {
      method: "post",
      body: formData,
    })
      .then((res) => res.json())
      .then((data) => {
        Axios.post("https://sweetbunny.herokuapp.com/create-item", {
          name: name,
          price: price,
          description: description,
          quantity: quantity,
          url: data.url,
          productType: type,
        }).then((response) => {
          if (response.data.err) {
            props.setMessage(response.data.err);
            console.log(response);
          } else {
            props.setMessage("Successfully added one product. (1)");
            props.setState("Added product");
            setTimeout(() => {
              props.setMessage("");
              props.setState("");
            }, 5000);

            setName("");
            setType("");
            setDescription("");
            setPrice("");
            setQuantity("");
            setAfter(false);
            props.setShowForm(false);
          }
        });
      })
      .catch((err) => console.log(err));
  };

  return (
    <div className="product-form">
      {after ? (
        <div className="product-form-after">
          <p>Loading. Please wait</p>
          <div>
            <PropagateLoader color={`#E28A46`} size={13} loading />
          </div>
        </div>
      ) : null}
      <div className="product-form-header">
        <p>Add Product</p>
        <i
          onClick={() => props.setShowForm(false)}
          className="fas fa-times"
        ></i>
      </div>
      <div className="product-form-body">
        <select
          onChange={(e) => setType(e.target.value)}
          value={type}
          className="form-select"
        >
          <option disabled value="">
            Category
          </option>
          <option value="Foods">Foods</option>
          <option value="Drinks">Drinks</option>
        </select>
        <input
          onChange={(e) => setName(e.target.value)}
          className="form-input"
          value={name}
          type="text"
          placeholder="Product Name"
        />
        <input
          onChange={(e) => setDescription(e.target.value)}
          value={description}
          className="textarea"
          placeholder="Product Description (Optional)"
        />
        <input
          onChange={(e) => setQuantity(e.target.value)}
          value={quantity}
          className="form-input"
          type="number"
          placeholder="Quantity"
        />
        <input
          onChange={(e) => setPrice(e.target.value)}
          value={price}
          className="form-input"
          type="number"
          placeholder="Price - "
        />

        <div className="input-div-file">
          <label>Picture </label>
          <div className="input-div-file-input">
            <input
              type="file"
              className="custom-file-input"
              onChange={(e) => {
                setPicture(e.target.files[0]);
              }}
            />
          </div>
        </div>

        <div className="submit-btn-div">
          <input
            onClick={() => props.setShowForm(false)}
            className="form-cancel-btn"
            type="submit"
            value="Cancel"
          />
          <input
            onClick={upload}
            className={
              name === "" ||
              type === "" ||
              quantity === "" ||
              price === "" ||
              picture === ""
                ? "form-add-btn-diactive"
                : "form-add-btn"
            }
            type="submit"
            value="Add"
          />
        </div>
      </div>
    </div>
  );
};

export default Form;
