import React, { useContext, useEffect } from "react";
import "./Card.css";
import Axios from "axios";
import { ContextFile } from "../../Context/ContextFile";

const Card = (props) => {
  const addToCart = () => {
    props.setState("as");
    props.setAdd("adding");
    Axios.put(`https://sweetbunny.herokuapp.com/add-cart/${props.userId}`, {
      id: props.id,
      name: props.name,
      price: props.price,
      picture: props.image,
      productType: props.category,
    }).then((response) => {
      if (response.data.success === "Added one item to your cart. (1)") {
        props.setAdd("success");
        props.setState("Added");
        setTimeout(() => {
          props.setAdd("");
          props.setState("");
        }, 5000);
      }
    });
  };

  const { firstnameState } = useContext(ContextFile);

  const [firstname, setFirstname] = firstnameState;

  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/login").then((response) => {
      if (response.data.loggedIn === false) {
        setFirstname("");
      } else {
        setFirstname(response.data.firstname);
      }
    });
  }, [props.state]);
  return (
    <div className="card-item">
      <div className="card-top">
        <img src={props.image} />
        <div
          onClick={addToCart}
          className={
            firstname === "" ? "card-add-to-cart-diactive" : "card-add-to-cart"
          }
        >
          Add to cart
        </div>
      </div>
      <div className="card-btm">
        <h4>{props.name}</h4>

        <p> {props.price}</p>
      </div>
    </div>
  );
};

export default Card;
