import React, { useState } from "react";
import "./Delete.css";
import Axios from "axios";
import { PropagateLoader } from "react-spinners";

const Delete = (props) => {
  const submitDelete = () => {
    setShow(true);
    Axios.put("https://sweetbunny.herokuapp.com/delete-item", {
      id: props.id,
    }).then((response) => {
      if (response.data.success === "success") {
        props.setSuccess("success");
        setShow(false);
      }
    });
  };

  const [show, setShow] = useState(false);
  return (
    <div className="delete">
      {show ? (
        <div className="delete-after">
          <PropagateLoader color={`#E28A46`} size={13} loading />
        </div>
      ) : null}
      <div className="delete-top">
        <p style={{ fontWeight: 500, marginBottom: "10px" }}>
          Are you sure delete this product?
        </p>
        <p style={{ fontWeight: 300, fontSize: "14PX" }}>
          If you delete a product you will no longer be able to recover it. It
          will permanently be deleted.
        </p>
      </div>
      <div className="delete-btm">
        <input
          onClick={() => {
            props.setShowDelete(false);
          }}
          className="cancel-delete"
          type="submit"
          value="Cancel"
        />
        <input
          onClick={submitDelete}
          className="delete-btn"
          type="submit"
          value="Delete"
        />
      </div>
    </div>
  );
};

export default Delete;
