import React, { useState, useContext, useEffect } from "react";
import "./Login.css";
import { Link, Redirect } from "react-router-dom";
import Error from "../Components/Error/Error";
import Axios from "axios";
import { ContextFile } from "../Context/ContextFile";
import { useHistory } from "react-router-dom";

const Login = (props) => {
  const { roleState, firstnameState, stateState, logState } =
    useContext(ContextFile);

  const [role, setRole] = roleState;
  const [firstname, setFirstname] = firstnameState;
  const [state, setState] = stateState;
  const [loggedIn, setLoggedIn] = logState;

  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [errMsg, setErrMsg] = useState("");

  const history = useHistory();
  const goBack = () => {
    history.goBack();
  };

  Axios.defaults.withCredentials = true;

  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/login").then((response) => {
      if (response.data.loggedIn === false) {
        setRole("");
      } else {
        setRole(response.data.user);
      }
    });
  }, [props.state]);

  const submitLogin = () => {
    props.setState("Logging in");
    Axios.post("https://sweetbunny.herokuapp.com/login", {
      username: username,
      password: password,
    }).then((response) => {
      if (response.data.err) {
        setErrMsg(response.data.err);
        setState("login");
      } else {
        setErrMsg("");
        window.location.reload();
        setState("login");
        props.setState("Logged in");
        setTimeout(() => props.setState("SD"), 1000);
      }
    });
  };
  return (
    <>
      {role === "Customer" && <Redirect to="/" />}
      {role === "Admin" && <Redirect to="/admin" />}
      <div className="login-wrapper">
        <div className="login-left"></div>
        <div className="login-right">
          {errMsg === "" ? null : <Error error={errMsg} />}
          <div className="login-right-top">
            <h2>Sign In to Sweet Bunny Cafe.</h2>
            <p>Everything's will be better with a coffee and desserts</p>
          </div>
          <form
            onSubmit={(e) => e.preventDefault()}
            className="login-right-btm"
          >
            <div className="username">
              <i className="fas fa-user"></i>
              <input
                onChange={(e) => setUsername(e.target.value)}
                value={username}
                type="text"
                placeholder="Username"
              />
            </div>
            <div className="password">
              <i className="fas fa-key"></i>
              <input
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                type={showPassword === false ? "password" : "text"}
                placeholder="Password"
              />
              {password === "" ? null : (
                <div>
                  <i
                    onClick={() => setShowPassword(!showPassword)}
                    className={
                      showPassword === false ? "far fa-eye-slash" : "far fa-eye"
                    }
                  ></i>
                </div>
              )}
            </div>

            <div className="sign-wrapper">
              <div className="sign-links">
                <Link to="/sweetbunny/register" className="register-link">
                  Register
                </Link>
                <Link className="forgot-link">Forgot password?</Link>
              </div>
              <input
                onKeyPress={(e) => {
                  e.key === "Enter" && e.preventDefault();
                }}
                className={
                  username === "" || password === ""
                    ? "login-btn-diactive"
                    : "login-btn"
                }
                onClick={submitLogin}
                type="submit"
                value="Sign In"
              />
            </div>
          </form>

          <div className="copyright">
             2021 Sweet Bunny Cafe | All rights reserved
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
