import React, { useState, useContext, useEffect } from "react";
import "./Cart.css";
import Navbar from "../../Components/Navbar/Navbar";
import Footer from "../../Components/Footer/Footer";
import { Link, Redirect } from "react-router-dom";
import { ContextFile } from "../../Context/ContextFile";
import Axios from "axios";
import Gcash from "./gcash.png";
import Paymaya from "./paymaya.png";
import Master from "./master.png";
import cod from "./cod.png";
import { HashLoader } from "react-spinners";
import AddressForm from "../../Components/AddressForm/AddressForm";
import { SettingsRemoteRounded } from "@material-ui/icons";

const Cart = (props) => {
  const {
    firstnameState,
    numberState,
    userState,
    cartState,
    roleState,
    cityState,
    barangayState,
    streetState,
  } = useContext(ContextFile);
  const [firstname, setFirstname] = firstnameState;
  const [number, setNumber] = numberState;

  const [userId, setUserId] = userState;

  const [cart, setCart] = cartState;
  const [active, setActive] = useState(false);

  const [change, setChange] = useState("");
  const [role, setRole] = roleState;

  const [city, setCity] = cityState;
  const [street, setStreet] = streetState;
  const [barangay, setBarangay] = barangayState;

  const [add, setAdd] = useState("");

  const [remove, setRemove] = useState("");
  const [removeAll, setRemoveAll] = useState(false);

  const [gcashNumber, setGcashNumber] = useState(0);
  const [payNumber, setPayNumber] = useState(0);
  const [debitNumber, setDebitNumber] = useState(0);
  const [expiration, setExpirate] = useState("");
  const [cvc, setCvc] = useState(0);
  const [ownerName, setOwnerName] = useState("");

  const removeAllItem = () => {
    setConfirmLoading(true);
    Axios.put(`https://sweetbunny.herokuapp.com/remove-all/${userId}`).then(
      (response) => {
        if (response.data.err) {
          setConfirmLoading(false);
          setRemoveAll(false);
          console.log(response.data.err);
        } else {
          setConfirmLoading(false);
          setRemove("All items removed");
          setTimeout(() => {
            setRemove("");
          }, 5000);
          setAfter(false);
          setRemoveAll(false);
        }
      }
    );
  };

  const removeItem = (ids) => {
    setAfter(true);
    Axios.put(
      `https://sweetbunny.herokuapp.com/remove-item/${userId}/${ids}`
    ).then((response) => {
      if (response.data.err) {
        console.log(response.data.err);
        setAfter(false);
      } else {
        setRemove("Removed one item from your cart");
        setTimeout(() => {
          setRemove("");
        }, 5000);
        setAfter(false);
      }
    });
  };

  useEffect(() => {
    setPayNumber(`+63 ${number}`);
    setGcashNumber(`+63 ${number}`);
    Axios.get(`https://sweetbunny.herokuapp.com/add-cart/${userId}`)
      .then((response) => {
        if (response.data.cart) {
          setCart(response.data.cart);
        }
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  }, [props.state, remove]);

  useEffect(() => {
    Axios.get(`https://sweetbunny.herokuapp.com/address/${userId}`)
      .then((response) => {
        if (
          response.data.city === "" ||
          response.data.street === "" ||
          response.data.barangay === ""
        ) {
          setBarangay("");
          setStreet("");
          setCity("");
        } else {
          setBarangay(response.data.barangay);
          setStreet(response.data.street);
          setCity(response.data.city);
        }
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  }, [props.state]);

  const changeAccount = () => {
    Axios.get("https://sweetbunny.herokuapp.com/logout")
      .then((response) => {
        if (response) {
          props.setState("changing");
          setRole("");
          setCart([]);
          setTimeout(() => setChange("change"), 1000);
        }
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  };

  const [address, setAddress] = useState(false);
  const [after, setAfter] = useState(false);

  const [method, setMethod] = useState("");

  const [confirmLoading, setConfirmLoading] = useState(false);

  const [paid, setPaid] = useState(false);

  const date = new Date();
  let dayDate = date.getDate();
  let monthDate = date.getMonth() + 1;
  let yearDate = date.getFullYear();

  const payItem = () => {
    Axios.put(`https://sweetbunny.herokuapp.com/order/${userId}`, {
      date: monthDate + "/" + dayDate + "/" + yearDate,
      total: cart.map((e) => e.price).reduce((a, b) => a + b, 20),
      method: method,
      status: "Pending",
    })
      .then((response) => {
        if (response.data.success) {
          removeAllItem();
          setPaid(true);
          props.setState("Paid");
        }
      })
      .catch((error) => console.log(error.data));
  };
  return (
    <>
      {paid && <Redirect to="/order" />}
      {change === "change" && <Redirect to="/login" />}
      <div className="cart-wrapper">
        {address && <div className="cart-wrapper-after"></div>}
        {after && <div className="cart-wrapper-after"></div>}
        {removeAll && (
          <div className="pop">
            {confirmLoading && (
              <div className="pop-after">
                <HashLoader size={35} color={`#E28A46`} loading={true} />
              </div>
            )}
            <div className="pop-top">
              <p>Are you sure you want to remove all the items in your cart?</p>
            </div>
            <div className="pop-bottom">
              <input
                onClick={() => {
                  setRemoveAll(false);
                  setAfter(false);
                }}
                className="cancel-input-pop"
                type="submit"
                value="Cancel"
              />
              <input
                onClick={() => removeAllItem()}
                className="confirm-input-pop"
                type="submit"
                value="Confirm"
              />
            </div>
          </div>
        )}

        <Navbar state={props.state} cartLength={cart.length} />
        {add === "add" ? (
          <div className="menu-toast">
            <div className="menu-toast-header">
              <p>Notice</p>
              <i onClick={() => setAdd("")} className="fas fa-times"></i>
            </div>
            <div className="menu-toast-body">
              <p>You updated your shipping address.</p>
            </div>
          </div>
        ) : null}
        {address ? (
          <AddressForm
            setAdd={setAdd}
            userId={userId}
            setAddress={setAddress}
            setState={props.setState}
          />
        ) : null}
        <div className="blank"></div>

        <div className="cart-page">
          <div className="cart-page-header">
            <h2>Shopping Cart</h2>
          </div>
          <div className="cart-page-directory">
            <Link
              style={{ color: "rgb(14, 96, 247)", textDecoration: "none" }}
              to="/sweetbunny/menu"
            >
              Menu 
            </Link>{" "}
            <Link style={{ color: "grey", textDecoration: "none" }}>
              Shopping Cart
            </Link>
          </div>

          {userId === "" ? (
            <p>You are currently not log in</p>
          ) : (
            <div className="cart-page-body">
              <div className="cart-left">
                <div className="cart-login">
                  <div onClick={changeAccount} className="change">
                    Change
                  </div>
                  <div className="cart-login-float">a</div>
                  <p style={{ color: "grey", marginBottom: "5px" }}>
                    LOGIN
                    <i
                      style={{ marginLeft: "10px", color: "rgb(20, 145, 47)" }}
                      className="fas fa-check"
                    ></i>
                  </p>

                  <div className="cart-login-details">
                    <p style={{ marginRight: "20px" }}>{firstname}</p>
                    <p>+63 {number}</p>
                  </div>
                </div>

                <div className="cart-login">
                  <div onClick={() => setAddress(true)} className="change">
                    Change
                  </div>
                  <div className="cart-login-float">b</div>
                  <p style={{ color: "grey", marginBottom: "5px" }}>
                    SHIPPING ADDRESS{" "}
                    {street || city || barangay ? (
                      <i
                        style={{
                          marginLeft: "10px",
                          color: "rgb(20, 145, 47)",
                        }}
                        className="fas fa-check"
                      ></i>
                    ) : null}
                  </p>

                  <div className="cart-login-details">
                    {street || city || barangay ? (
                      <div>
                        <p>
                          {street}, {barangay}
                        </p>

                        <p>{city}, Philippines </p>
                      </div>
                    ) : (
                      "No address set"
                    )}
                  </div>
                </div>

                <div className="payment-method">
                  <div>b</div> <p>PAYMENT METHOD</p>
                </div>

                {cart.length !== 0 ? (
                  <>
                    <div className="payment-option">
                      <div
                        onClick={() => setMethod("debit")}
                        className="payment-debit"
                      >
                        <div
                          className={
                            method === "debit" ? "tick-active" : "tick"
                          }
                        ></div>
                        <div
                          className={
                            method === "debit"
                              ? "payment-logo-active"
                              : "payment-logo"
                          }
                        >
                          <img src={Master} alt="MasterCard" />
                        </div>
                        <h4>Debit / Credit Card </h4>
                      </div>

                      {method === "debit" ? (
                        <div className="debit-form">
                          <label>Enter Card Number *</label>
                          <input
                            onChange={(e) => setDebitNumber(e.target.value)}
                            value={debitNumber}
                            className="card-number"
                            placeholder="Valid Card Number"
                            type="number"
                          />

                          <div className="debit-form-div">
                            <div className="debit-form-div-div">
                              <label>Expiration Date*</label>
                              <input type="date" />
                            </div>
                            <div className="debit-form-div-div">
                              <label>CV Code*</label>
                              <input
                                value={cvc}
                                onChange={(e) => setCvc(e.target.value)}
                                placeholder="CVC"
                                type="number"
                              />
                            </div>
                          </div>

                          <label>Card Owner*</label>
                          <input
                            value={ownerName}
                            onChange={(e) => setOwnerName(e.target.value)}
                            className="card-number"
                            placeholder="Card Owner Name"
                            type="text"
                          />

                          <div className="debit-pay">
                            <input
                              onClick={payItem}
                              className={
                                debitNumber === 0 ||
                                debitNumber === "" ||
                                debitNumber === null ||
                                cvc === 0 ||
                                cvc === "" ||
                                cvc === null ||
                                ownerName === 0 ||
                                ownerName === "" ||
                                ownerName === null
                                  ? "pay-btn-diactive"
                                  : "pay-btn"
                              }
                              type="submit"
                              value="Pay now"
                            />
                          </div>
                        </div>
                      ) : null}
                    </div>
                    <div className="payment-option">
                      <div
                        onClick={() => setMethod("GCash")}
                        className="payment-debit"
                      >
                        <div
                          className={
                            method === "GCash" ? "tick-active" : "tick"
                          }
                        ></div>
                        <div
                          className={
                            method === "GCash"
                              ? "payment-logo-active"
                              : "payment-logo"
                          }
                        >
                          <img src={Gcash} alt="Gcash" />
                        </div>
                        <h4>Gcash</h4>
                      </div>

                      {method === "GCash" ? (
                        <div className="debit-form">
                          <label>Confirm Number*</label>
                          <input
                            onChange={(e) => setGcashNumber(e.target.value)}
                            value={gcashNumber}
                            className="card-number"
                            placeholder="Gcash Number"
                            type="text"
                          />

                          <div className="debit-pay">
                            <input
                              onClick={payItem}
                              className={
                                gcashNumber === 0 ||
                                gcashNumber === "" ||
                                gcashNumber === null
                                  ? "pay-btn-diactive"
                                  : "pay-btn"
                              }
                              type="submit"
                              value="Pay now"
                            />
                          </div>
                        </div>
                      ) : null}
                    </div>
                    <div className="payment-option">
                      <div
                        onClick={() => setMethod("paymaya")}
                        className="payment-debit"
                      >
                        <div
                          className={
                            method === "paymaya" ? "tick-active" : "tick"
                          }
                        ></div>
                        <div
                          className={
                            method === "paymaya"
                              ? "payment-logo-active"
                              : "payment-logo"
                          }
                        >
                          <img src={Paymaya} alt="PayMaya" />
                        </div>
                        <h4>PayMaya</h4>
                      </div>

                      {method === "paymaya" ? (
                        <div className="debit-form">
                          <label>Confirm Number*</label>
                          <input
                            onChange={(e) => setPayNumber(e.target.value)}
                            value={payNumber}
                            className="card-number"
                            placeholder="Paymaya Number"
                            type="text"
                          />

                          <div className="debit-pay">
                            <input
                              onClick={payItem}
                              className={
                                payNumber === 0 ||
                                cart.length === 0 ||
                                payNumber === "" ||
                                cart.length === 0 ||
                                payNumber === null ||
                                cart.length === 0
                                  ? "pay-btn-diactive"
                                  : "pay-btn"
                              }
                              type="submit"
                              value="Pay now"
                            />
                          </div>
                        </div>
                      ) : null}
                    </div>
                    <div className="payment-option">
                      <div
                        onClick={() => setMethod("cod")}
                        className="payment-debit"
                      >
                        <div
                          className={method === "cod" ? "tick-active" : "tick"}
                        ></div>
                        <div
                          className={
                            method === "cod"
                              ? "payment-logo-active"
                              : "payment-logo"
                          }
                        >
                          <img src={cod} alt="COD" />
                        </div>
                        <h4>Cash On Delivery</h4>
                      </div>

                      {method === "cod" ? (
                        <div className="debit-pay">
                          <input
                            onClick={payItem}
                            className={
                              cart.length === 0 ? "pay-btn-diactive" : "pay-btn"
                            }
                            type="submit"
                            value="Pay now"
                          />
                        </div>
                      ) : null}
                    </div>
                  </>
                ) : null}
              </div>
              <div className="cart-right">
                <div className="cart-right-header">
                  <h3>Your Order</h3>
                  {cart.length !== 0 && (
                    <p
                      onClick={() => {
                        setRemoveAll(true);
                        setAfter(true);
                      }}
                    >
                      Remove all
                    </p>
                  )}
                </div>

                <div className="cart-right-body">
                  {cart.map((e) => {
                    return (
                      <div key={e._id} className="cart-right-body-wrapper">
                        <i
                          onClick={() => removeItem(e._id)}
                          className="fas fa-times"
                        ></i>
                        <div className="wrapper-image-wrapper">
                          <img src={e.picture} alt="Product" />
                        </div>
                        <div className="wrapper-text-wrapper">
                          <p
                            style={{
                              fontWeight: 500,
                              fontSize: "15px",
                              marginBottom: "10px",
                            }}
                          >
                            {e.productName}
                          </p>

                          <p style={{ fontWeight: 300 }}> {e.price}</p>
                        </div>
                      </div>
                    );
                  })}
                </div>

                {remove !== "" && (
                  <div className="menu-toast">
                    <p>{remove}</p>
                    <i onClick={() => setAdd("")} className="fas fa-times"></i>
                  </div>
                )}

                <div className="cart-right-footer">
                  <div className="delivery-fee">
                    <div>
                      <p style={{ marginBottom: "5px" }}>Delivery Fee</p>
                      <p>Sub Total</p>
                    </div>
                    <div>
                      <div style={{ display: "flex", marginBottom: "5px" }}>
                        {cart.length === 0 ? (
                          <p>
                             0
                            <span
                              style={{
                                fontWeight: 300,
                                color: "grey",
                                marginLeft: "10px",
                              }}
                            ></span>
                          </p>
                        ) : (
                          <p>
                             20
                            <span
                              style={{
                                fontWeight: 300,
                                color: "grey",
                                marginLeft: "10px",
                              }}
                            ></span>
                          </p>
                        )}
                      </div>
                      <p>
                         {cart.map((e) => e.price).reduce((a, b) => a + b, 0)}
                      </p>
                    </div>
                  </div>

                  <div className="total-fee">
                    <p>Total</p>
                    {cart.length === 0 ? (
                      <p>
                         {cart.map((e) => e.price).reduce((a, b) => a + b, 0)}
                        .00
                      </p>
                    ) : (
                      <p>
                         {cart.map((e) => e.price).reduce((a, b) => a + b, 20)}
                        .00
                      </p>
                    )}
                  </div>
                </div>
              </div>
            </div>
          )}
        </div>
        <Footer />
      </div>
    </>
  );
};

export default Cart;
