import React, { useState, useContext, useEffect } from "react";
import "./Admin.css";
import { Link, Redirect } from "react-router-dom";
import AddRoundedIcon from "@material-ui/icons/AddRounded";
import Form from "../../Components/Form/Form";
import DashboardHeader from "../../Components/DashboardHeader/DashboardHeader";
import { ContextFile } from "../../Context/ContextFile";
import Dashboard from "../../Components/Dashboard/Dashboard";
import Toast from "../../Components/Toast/Toast";
import Axios from "axios";

const Admin = (props) => {
  const [showForm, setShowForm] = useState(false);
  const { roleState, messageState, stateState } = useContext(ContextFile);

  const [role, setRole] = roleState;
  const [message, setMessage] = useState("");

  const [product, setProduct] = useState([]);
  const [state, setState] = stateState;

  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/create-item").then(
      (response) => {
        if (response.data === 0) {
          setProduct([]);
        } else {
          setProduct(response.data);
        }
      }
    );
  }, [props.state]);

  return (
    <>
      {role !== "Admin" && <Redirect to="/" />}
      <div className="admin-wrapper">
        {showForm === false ? null : (
          <Form
            state={props.state}
            setMessage={setMessage}
            setState={props.setState}
            setShowForm={setShowForm}
          />
        )}
        {message === "" ? null : <Toast message={message} />}

        <Dashboard />
        <div className="admin-content">
          <div className="admin-content-wrapper">
            <DashboardHeader />

            <div className="admin-body">
              <div className="list-of-product">
                <div className="product-header">
                  <p>List of Products</p>
                </div>

                <div className="product-body">
                  <div className="list-header">
                    <div className="product-no">
                      <h5>No.</h5>
                    </div>
                    <div className="product-name">
                      <h5>Product</h5>
                    </div>
                    <div className="product-quantity">
                      <h5>Stocks</h5>
                    </div>
                    <div className="product-price">
                      <h5>Price</h5>
                    </div>
                    <div className="product-category">
                      <h5>Category</h5>
                    </div>
                  </div>
                  <div className="list-body-wrapper">
                    {product.map((e, key) => {
                      return (
                        <>
                          <Link
                            to={"/sweetbunny/admin/product/" + e._id}
                            className="list-body"
                          >
                            <div className="product-no">
                              <p>{key + 1}</p>
                            </div>
                            <div className="product-name">
                              <div className="product-image-wrapper">
                                <img src={e.picture} alt="Product" />
                              </div>
                              <p>{e.name}</p>
                            </div>
                            <div className="product-quantity">
                              <p>{e.quantity}</p>
                            </div>
                            <div className="product-price">
                              <p>  {e.price}</p>
                            </div>
                            <div className="product-category">
                              <p>{e.productType}</p>
                            </div>
                          </Link>
                        </>
                      );
                    })}
                  </div>

                  <div
                    onClick={() => setShowForm(!showForm)}
                    className="add-btn"
                  >
                    <AddRoundedIcon />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Admin;
