import React, { useState } from "react";
import "./Register.css";
import { Link } from "react-router-dom";
import Error from "../../Components/Error/Error";
import Success from "../../Components/Success/Success";
import Axios from "axios";

const Register = () => {
  const [firstname, setFirstname] = useState("");
  const [lastname, setLastname] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [contact, setContact] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const [errMsg, setErrMsg] = useState("");

  const submitRegister = () => {
    Axios.post("https://sweetbunny.herokuapp.com/register", {
      username: username,
      password: password,
      firstname: firstname,
      lastname: lastname,
      contact: contact,
    }).then((response) => {
      if (response.data.err) {
        setErrMsg(response.data.err);
      } else {
        setErrMsg(response.data.success);
        setUsername("");
        setPassword("");
        setFirstname("");
        setLastname("");
        setContact("");
        setTimeout(() => setErrMsg(""), 5000);
      }
    });
  };

  return (
    <>
      <div className="register-wrapper">
        <div className="register-left"></div>
        <div className="register-right">
          {errMsg === "" ? null : <Error error={errMsg} />}
          {errMsg === "Account successfully created." ? (
            <Success success={errMsg} />
          ) : null}
          <div className="register-right-top">
            <h2>Register to Sweet Bunny Cafe.</h2>
            <p>Everything's will be better with a coffee and desserts</p>
          </div>
          <form
            onSubmit={(e) => e.preventDefault()}
            className="register-right-btm"
          >
            <input
              onChange={(e) => setFirstname(e.target.value)}
              value={firstname}
              type="text"
              placeholder="First Name"
            />
            <input
              onChange={(e) => setLastname(e.target.value)}
              value={lastname}
              type="text"
              placeholder="Last Name"
            />

            <input
              onChange={(e) => setUsername(e.target.value)}
              value={username}
              type="text"
              placeholder="Username"
            />
            <div className="password">
              <input
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                type={showPassword === false ? "password" : "text"}
                placeholder="Password"
              />
              {password === "" ? null : (
                <div>
                  <i
                    onClick={() => setShowPassword(!showPassword)}
                    className={
                      showPassword === false ? "far fa-eye-slash" : "far fa-eye"
                    }
                  ></i>
                </div>
              )}
            </div>
            <div className="contact-input">
              <p>+63</p>
              <input
                onChange={(e) => setContact(e.target.value)}
                value={contact}
                type="text"
                maxLength="10"
                placeholder="Contact No."
              />
            </div>

            <div className="sign-up-wrapper">
              <div className="sign-links">
                <Link to="/sweetbunny/login" className="register-link">
                  Already have an account? Login
                </Link>
              </div>
              <input
                onClick={submitRegister}
                className="register-btn"
                type="submit"
                value="Register"
              />
            </div>
          </form>

          <div className="copyright">
             2021 Sweet Bunny Cafe | All rights reserved
          </div>
        </div>
      </div>
    </>
  );
};

export default Register;
