import React, { useState } from "react";
import "./DashboardHeader.css";
import Axios from "axios";

const DashboardHeader = () => {
  const [profileDropdown, setProfileDropdown] = useState(false);
  const [notificaitonDropdown, setNotificationDropdown] = useState(false);

  const submitLogout = () => {
    Axios.get("https://sweetbunny.herokuapp.com/logout")
      .then((response) => {
        window.location.reload();
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  };
  return (
    <>
      <div className="admin-header">
        <div className="admin-header-wrapper">
          <h3>Admin Dashboard </h3>

          <div className="admin-profile">
            <div
              onClick={() => {
                setProfileDropdown(false);
                setNotificationDropdown(!notificaitonDropdown);
              }}
              className={
                notificaitonDropdown
                  ? "notification-drop-active"
                  : "notification-drop"
              }
            >
              <i className="fas fa-bell"></i>
              {notificaitonDropdown ? (
                <div className="notification-drop-after">
                  <div className="notification-body">
                    <h3>Notifications</h3>
                  </div>
                  <div className="see-all-notification">
                    <p> See all notifications</p>
                  </div>
                </div>
              ) : null}
            </div>
            <div
              onClick={() => {
                setProfileDropdown(!profileDropdown);
                setNotificationDropdown(false);
              }}
              className={
                profileDropdown ? "profile-drop-active" : "profile-drop"
              }
            >
              <i className="fas fa-user"></i>
              {profileDropdown ? (
                <div onClick={submitLogout} className="profile-drop-after">
                  <p>LOGOUT</p>
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default DashboardHeader;
