import React, { useState, useEffect, useContext } from "react";
import "./AddressForm.css";
import { Barangay } from "./Barangay";
import Axios from "axios";
import { ContextFile } from "../../Context/ContextFile";

const AddressForm = (props) => {
  const [city, setCity] = useState("Zamboanga City");
  const [street, setStreet] = useState("");
  const [barangay, setBarangay] = useState("");

  const updateAddress = () => {
    props.setState("seeting address");
    Axios.put(`https://sweetbunny.herokuapp.com/address/${props.userId}`, {
      street: street,
      city: city,
      barangay: barangay,
    })
      .then((response) => {
        if (response.data.err) {
          console.log(response.data.err);
        } else {
          props.setState("ss");
          props.setAdd("add");
          setTimeout(() => {
            props.setAdd("");
            props.setState("");
          }, 8000);
          props.setAddress(false);
        }
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  };
  return (
    <div className="address-form">
      <div className="address-form-header">
        <h3>Shipping Address</h3>
        <i onClick={() => props.setAddress(false)} className="fas fa-times"></i>
      </div>

      <div className="address-form-body">
        <label>Barangay</label>
        <select value={barangay} onChange={(e) => setBarangay(e.target.value)}>
          {Barangay.map((e) => {
            return (
              <option key={e.name} value={e.name}>
                {e.name}
              </option>
            );
          })}
        </select>
        <label>Street</label>
        <input
          value={street}
          onChange={(e) => setStreet(e.target.value)}
          type="text"
          placeholder="Landmark, Street No., Etc."
        />
        <label>City</label>
        <input
          onChange={(e) => setCity(e.target.value)}
          value={city}
          type="text"
        />
        <div className="submit-wrapper">
          <input
            onClick={() => props.setAddress(false)}
            type="submit"
            className="address-cancel-btn"
            value="Cancel"
          />
          <input
            onClick={updateAddress}
            type="submit"
            className="address-submit-btn"
            value="Update"
          />
        </div>
      </div>
    </div>
  );
};

export default AddressForm;
