import React, { useState } from "react";
import "./ReservationForm.css";
import Book from "./book.svg";

const ReservationForm = (props) => {
  const [table, setTable] = useState("");
  return (
    <div className="reservation-form">
      <i onClick={() => props.setShowForm(false)} className="fas fa-times"></i>
      <div className="reserve-left">
        <div className="reservation-header">
          <h3>Make an appointment</h3>
        </div>

        <div className="reservation-body">
          <div className="reserve-table">
            <div
              onClick={() => setTable(1)}
              className={
                table === 1 ? "reserve-table-item-active" : "reserve-table-item"
              }
            >
              <div className={table === 1 ? "active" : "inactive"}></div> Table
              for 1
            </div>
            <div
              onClick={() => setTable(2)}
              className={
                table === 2 ? "reserve-table-item-active" : "reserve-table-item"
              }
            >
              {" "}
              <div className={table === 2 ? "active" : "inactive"}></div>Table
              for 2
            </div>
            <div
              onClick={() => setTable(3)}
              className={
                table === 3 ? "reserve-table-item-active" : "reserve-table-item"
              }
            >
              {" "}
              <div className={table === 3 ? "active" : "inactive"}></div>Table
              for 3
            </div>
            <div
              onClick={() => setTable(4)}
              className={
                table === 4 ? "reserve-table-item-active" : "reserve-table-item"
              }
            >
              {" "}
              <div className={table === 4 ? "active" : "inactive"}></div>Table
              for 4 or more
            </div>
          </div>
        </div>
      </div>
      <div className="reserve-right">
        <img src={Book} />
      </div>
    </div>
  );
};

export default ReservationForm;
