import React, { useState } from "react";
import Dashboard from "../../Components/Dashboard/Dashboard";
import DashboardHeader from "../../Components/DashboardHeader/DashboardHeader";
import "./Product.css";
import EditForm from "../../Components/Form/EditForm";
import Delete from "../../Components/Delete/Delete";
import { Redirect } from "react-router-dom";

const Product = (props) => {
  const [showForm, setShowForm] = useState(false);
  const [showDelete, setShowDelete] = useState(false);
  const [success, setSuccess] = useState("");
  return (
    <>
      {success === "success" && <Redirect to="/admin" />}
      <div className="admin-wrapper">
        {showDelete ? <div className="admin-wrapper-after"></div> : null}
        {showForm ? <EditForm setShowForm={setShowForm} /> : null}
        {showDelete ? (
          <Delete
            setSuccess={setSuccess}
            id={props.id}
            setShowDelete={setShowDelete}
          />
        ) : null}
        <Dashboard />
        <div className="admin-content">
          <DashboardHeader />

          <div className="product-wrapper">
            <div className="product-wrapper-left">
              <img src={props.image} />
            </div>

            <div className="product-wrapper-right">
              <div>
                <p
                  onClick={() => {
                    setShowForm(!showForm);
                    setShowDelete(false);
                  }}
                  className="product-p"
                >
                  Edit
                  <i className="fas fa-pen"></i>
                </p>
                <p
                  onClick={() => {
                    setShowDelete(!showDelete);
                    setShowForm(false);
                  }}
                  className="product-p-2"
                >
                  Delete <i className="fas fa-trash"></i>
                </p>
              </div>
              <h1>{props.name}</h1>
              <p style={{ fontSize: "20px" }}> {props.price}</p>
              <br />
              <br />

              <p
                style={{ color: "grey", fontSize: "14PX", marginBottom: "5px" }}
              >
                Product Description:
              </p>

              <p>
                {props.description === ""
                  ? "No description for this product."
                  : props.description}
              </p>
              <br />

              <p
                style={{ color: "grey", fontSize: "14PX", marginBottom: "5px" }}
              >
                Stocks (Available):{" "}
              </p>
              <p>{props.quantity}</p>

              <br />
              <p
                style={{ color: "grey", fontSize: "14PX", marginBottom: "5px" }}
              >
                Category:{" "}
              </p>
              <p>{props.category}</p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Product;
