import React, { useState, createContext } from "react";

export const ContextFile = createContext();

export const ContextProvider = (props) => {
  const [state, setState] = useState("");
  const [loggedIn, setLoggedIn] = useState(false);
  const [role, setRole] = useState("");
  const [firstname, setFirstname] = useState("");
  const [message, setMessage] = useState("");
  const [userId, setUserId] = useState("");
  const [cart, setCart] = useState([]);
  const [number, setNumber] = useState("");
  const [city, setCity] = useState("");
  const [street, setStreet] = useState("");
  const [barangay, setBarangay] = useState("");
  return (
    <ContextFile.Provider
      value={{
        roleState: [role, setRole],
        firstnameState: [firstname, setFirstname],
        stateState: [state, setState],
        logState: [loggedIn, setLoggedIn],
        messageState: [message, setMessage],
        userState: [userId, setUserId],
        cartState: [cart, setCart],
        numberState: [number, setNumber],
        cityState: [city, setCity],
        streetState: [street, setStreet],
        barangayState: [barangay, setBarangay],
      }}
    >
      {props.children}
    </ContextFile.Provider>
  );
};
