import React, { useState, useEffect, useContext } from "react";
import Footer from "../../Components/Footer/Footer";
import Card from "../../Components/Cards/Card";
import Navbar from "../../Components/Navbar/Navbar";
import Axios from "axios";
import { ContextFile } from "../../Context/ContextFile";
import "./Menu.css";
import { HashLoader } from "react-spinners";
import { Link } from "react-router-dom";

const Menu = (props) => {
  const [product, setProduct] = useState([]);
  const [searchWord, setSearchWord] = useState("");
  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/create-item").then(
      (response) => {
        if (response.data === 0) {
          setProduct([]);
        } else {
          setProduct(response.data);
        }
      }
    );
  }, [props.state]);

  const { roleState, logState, userState, numberState } =
    useContext(ContextFile);
  const [role, setRole] = roleState;
  const [userId, setUserId] = userState;

  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/login").then((response) => {
      if (response.data.loggedIn === false) {
        setRole("");
      } else {
        setRole(response.data.user);
        setUserId(response.data.id);
      }
    });
  }, [props.state]);

  const [add, setAdd] = useState("");

  return (
    <>
      <div className="menu-wrapper">
        {add === "adding" ? (
          <div className="menu-wrapper-after">
            <div className="menu-loader">
              <HashLoader size={35} color={`#E28A46`} loading={true} />
            </div>
          </div>
        ) : null}
        <div className="blank"></div>
        <Navbar state={props.state} />
        <div className="menu-banner">
          <div className="banner-wrapper">
            <h1>Delicious sweets and treats</h1>
            <p>Everything's will be better with a coffee and desserts</p>
          </div>
        </div>
        <div className="menu">
          {/* <div className="menu-after"></div> */}
          {add === "success" ? (
            <div className="menu-toast">
              <p>Added one item to your cart</p>
              <i onClick={() => setAdd("")} className="fas fa-times"></i>
            </div>
          ) : null}

          <div className="menu-nav">
            <h3>Browse by Category</h3>
            <div>
              <h4>Drinks</h4>
              <ul>
                {product
                  .filter((e) => e.productType === "Drinks")
                  .map((f) => {
                    return (
                      <Link className="menu-nav-list">
                        <li>{f.name}</li>
                      </Link>
                    );
                  })}
              </ul>
              <h4>Sweets</h4>
              <ul>
                {product
                  .filter((e) => e.productType === "Foods")
                  .map((f) => {
                    return (
                      <Link className="menu-nav-list">
                        <li>{f.name}</li>
                      </Link>
                    );
                  })}
              </ul>
            </div>
          </div>

          <div className="menu-btm">
            <div className="search-menu">
              <input
                onChange={(e) => {
                  setSearchWord(e.target.value);
                }}
                type="search"
                placeholder="Search product"
              />
            </div>
            <div className="menu-btm-div ">
              <div className="menu-btm-top">
                {product
                  .filter((val) => {
                    if (searchWord === "") {
                      return val;
                    } else if (
                      val.name.toLowerCase().includes(searchWord.toLowerCase())
                    ) {
                      return val;
                    }
                  })
                  .map((e) => {
                    return (
                      <Card
                        setState={props.setState}
                        userId={userId}
                        setAdd={setAdd}
                        price={e.price}
                        id={e._id}
                        name={e.name}
                        image={e.picture}
                        category={e.productType}
                      />
                    );
                  })}
              </div>
            </div>
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
};

export default Menu;
