import React, { useState, useEffect, useContext } from "react";
import Navbar from "../Components/Navbar/Navbar";
import { Link } from "react-router-dom";
import "./ClientOrder.css";
import Axios from "axios";
import { ContextFile } from "../Context/ContextFile";

const ClientOrder = (props) => {
  const [orders, setOrders] = useState([]);
  const { userState } = useContext(ContextFile);
  const [userId, setUserId] = userState;
  useEffect(() => {
    Axios.get(`https://sweetbunny.herokuapp.com/add-cart/${userId}`)
      .then((response) => {
        setOrders(response.data.order);
      })
      .catch((error) => {
        console.log(error.response.data);
      });
  }, [props.state]);

  const updateOrder = (ee) => {
    Axios.put(`https://sweetbunny.herokuapp.com/update-active/${ee}`).then(
      (response) => {
        console.log(response.data);
        props.setState("sds");
        setTimeout(() => props.setState(""), 1000);
      }
    );
  };

  return (
    <>
      <div className="order-wrapper">
        <Navbar />
        <div className="order-banner">
          <h1>You Orders</h1>
        </div>
        <div className="order-table-container">
          <div className="order-table">
            <div className="order-table-h3">
              <h3>Pending</h3>
            </div>
            <div className="table-header">
              <div className="header-no">#</div>
              <div className="header-date">Date</div>
              <div className="header-status">Total Price</div>
              <div className="header-action">Action</div>
            </div>
            {orders
              .filter((f) => f.status === "Pending")
              .map((e, index) => {
                return (
                  <div className="table-body">
                    <div className="header-no">{index + 1}</div>
                    <div className="header-date">{e.date}</div>
                    <div className="header-status"> {e.total}</div>
                    <div className="header-action">
                      <p
                        onClick={() => updateOrder(e._id)}
                        style={{
                          fontWeight: 500,
                          color: "green",
                        }}
                      >
                        Confirm
                      </p>{" "}
                    </div>
                  </div>
                );
              })}
          </div>

          <div className="order-received">
            <div className="order-table-h3">
              <h3>Orders Received</h3>
            </div>
            <div className="order-rec">
              <div className="table-header">
                <div className="header-no">#</div>
                <div style={{ width: "40%" }} className="header-date">
                  Date
                </div>
                <div style={{ width: "40%" }} className="header-status">
                  Total Price
                </div>
              </div>
              {orders
                .filter((f) => f.status === "Received")
                .map((e, index) => {
                  return (
                    <div className="table-body">
                      <div className="header-no">{index + 1}</div>
                      <div style={{ width: "40%" }} className="header-date">
                        {e.date}
                      </div>
                      <div style={{ width: "40%" }} className="header-status">
                         {e.total}
                      </div>
                    </div>
                  );
                })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ClientOrder;
