import React, { useState, useEffect } from "react";
import "./App.css";
import Login from "./Pages/Login";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Homepage from "./Pages/Homepage/Homepage";
import Services from "./Pages/Services/Services";
import Menu from "./Pages/Menu/Menu";
import Cart from "./Pages/Cart/Cart";
import About from "./Pages/About/About";
import Events from "./Pages/Events/Events";
import Register from "./Pages/Register/Register";
import Admin from "./Pages/Admin/Admin";
import ScrollToTop from "./Components/ScrollToTop/ScrollToTop";
import Orders from "./Pages/Orders/Orders";
import Settings from "./Pages/Settings/Settings";
import Message from "./Pages/Message/Message";
import { ContextProvider } from "./Context/ContextFile";
import Reservations from "./Pages/Reservations/Reservations";
import Axios from "axios";
import Product from "./Pages/Product/Product";
import ClientOrder from "./Pages/ClientOrder";

function App() {
  const [state, setState] = useState("");
  const [product, setProduct] = useState([]);

  useEffect(() => {
    Axios.get("https://sweetbunny.herokuapp.com/create-item").then(
      (response) => {
        if (response.data === 0) {
          setProduct([]);
        } else {
          setProduct(response.data);
        }
      }
    );
  }, [state]);
  return (
    <ContextProvider>
      <Router>
        <Switch>
          <Route path="/sweetbunny" exact>
            <Homepage state={state} />
          </Route>

          <Route path="/sweetbunny/services" exact>
            <Services />
          </Route>

          <Route path="/sweetbunny/menu" exact>
            <ScrollToTop>
              <Menu setState={setState} state={state} />
            </ScrollToTop>
          </Route>

          <Route path="/sweetbunny/cart" exact>
            <Cart state={state} setState={setState} />
          </Route>

          <Route path="/sweetbunny/order" exact>
            <ClientOrder state={state} setState={setState} />
          </Route>

          <Route path="/sweetbunny/events" exact>
            <Events />
          </Route>

          <Route path="/sweetbunny/about" exact component={About} about="/about" />

          <Route path="/sweetbunny/login" exact>
            <Login state={state} setState={setState} />
          </Route>

          <Route path="/sweetbunny/register" exact>
            <Register />
          </Route>

          <Route path="/sweetbunny/admin" exact>
            <Admin state={state} setState={setState} />
          </Route>

          <Route path="/sweetbunny/admin/orders" exact>
            <Orders />
          </Route>

          <Route path="/sweetbunny/admin/settings" exact>
            <Settings />
          </Route>

          <Route path="/sweetbunny/admin/message" exact>
            <Message />
          </Route>

          <Route path="/sweetbunny/admin/reservations" exact>
            <Reservations />
          </Route>

          {product.map((e) => {
            return (
              <Route key={e._id} path={"/sweetbunny/admin/product/" + e._id} exact>
                <Product
                  id={e._id}
                  image={e.picture}
                  name={e.name}
                  description={e.description}
                  price={e.price}
                  quantity={e.quantity}
                  category={e.productType}
                />
              </Route>
            );
          })}
        </Switch>
      </Router>
    </ContextProvider>
  );
}

export default App;
