import React from "react";
import Navbar from "../../Components/Navbar/Navbar";
import Service3 from "./Transaction.png";
import Service2 from "./Delivery.png";
import Service1 from "./Innovation.png";
import Footer from "../../Components/Footer/Footer";
import "./Services.css";

const Services = () => {
  return (
    <>
      <div className="blank"></div>
      <Navbar />
      <div className="services">
        <div className="services-wrapper">
          <div className="our-services">
            <h2 className="our">Our</h2>
            <h2>Services.</h2>
          </div>

          <div className="what-we-served">
            <h2>What do we served?</h2>
            <br />
            <p>
              The SweetBunny Cafe' serves the best and most satisfying Pastries,
              Desserts, Coffee's and Beverages in the town. We are also proud
              that we have the most unique and 100% pure ingredients that came
              from our nature and serve it for our cafe shop customers.Combining
              the Coffee with Pastries which our main product, it can help us to
              find way to make our lives and loves be better. It is actually the
              best comfort that we serve, the Coffee, the Pastries are proven
              and tested that to add colour that shines so bright and pours
              along with the sweetness in every Sip and Eat. We the SweetBunny
              Cafe' ensure that the one who will be the most satisfied in our
              services are our customers.{" "}
            </p>{" "}
            <br />
            <p>
              In our SweetBunny Cafe' website, we are serving online Booking in
              the Cafe and online Order that will be delivered in your place. We
              are also have the membership in this website to have the
              opportunity to update our valued customers regarding in our Cafe'
              Activities.
            </p>
          </div>
        </div>
      </div>

      <div className="services-2">
        <div className="services-icon-wrapper">
          <div className="services-icon">
            <div className="services-icon-icon">
              <img src={Service3} />
            </div>
            <div className="services-icon-des">
              <div className="services-des-title">
                <h3>Transaction</h3>
              </div>
              <div className="services-des-body">
                <p>
                  Keeping the rush for lattes with easy access to items and
                  favorites, and keep a card on file for regulars.
                </p>
              </div>
            </div>
          </div>
          <div className="services-icon">
            <div className="services-icon-icon">
              <img src={Service2} />
            </div>
            <div className="services-icon-des">
              <div className="services-des-title">
                <h3>Delivery</h3>
              </div>
              <div className="services-des-body">
                <p>
                  Free Delivery for the first 5km from the branch. Additional
                  fees may apply.
                </p>
              </div>
            </div>
          </div>
          <div className="services-icon">
            <div className="services-icon-icon">
              <img src={Service1} />
            </div>
            <div className="services-icon-des">
              <div className="services-des-title">
                <h3>Innovation</h3>
              </div>
              <div className="services-des-body">
                <p>
                  Innovation in cafe services can be seen in how the cafe is
                  changing and adapting to customer needs
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </>
  );
};

export default Services;
